.Container-Footer{
    display: grid;
    grid-template-rows: auto auto;
    height: 100%;
    padding-top: 25px;
    background-color: #2E2E2E;

}
.Container-Footer p {
    color: #F5F7F8; /* Color de texto en el footer */
}


.Footer-text ul p {
    margin-top: 5px;
    margin-bottom: 10px; /* Márgenes para todos los <p> dentro de <ul> */
}

.Container-Footer-A{

    display: grid;
    grid-template-rows: auto auto;
    margin-top: 30px;
    margin-bottom: 30px;
    margin-left: 125px;
    margin-right: 125px;

}
.Container-Footer-Text{
    display: grid;
    grid-template-columns: 33% 33% 33%;
    list-style-type: none;
}
.Footer-text-B p{
    margin-left: 125px;
    margin-right: 125px;
    color: #2E2E2E;
    margin-bottom: 20px;

}
.Wrap-Footer{
    display: grid;
}
.Wrap-Footer .Footer-Logo{
    height: 70px;
}
.Container-Footer-B{
    width: 100%;
    height:fit-content;
    background-color: #F5F7F8;
}
.Footer-text h3{
    color: #F5F7F8;
    text-align: left;
}

.F-izq{
    font-weight: bold;  
    font-size: large;
}

@media screen and (max-width: 720px) {
    .Container-Footer {
      grid-template-rows: auto; /* Ajusta el diseño a una sola fila */
      padding-top: 15px;
    }
  
    .Container-Footer p {
      font-size: 14px; /* Reduce el tamaño del texto */
      text-align: center; /* Centra el texto */
    }
  
    .Container-Footer-A {
      margin: 20px; /* Reduce márgenes */
    }
  
    .Container-Footer-Text {
      grid-template-columns: 1fr; /* Coloca los elementos en una sola columna */
      gap: 15px; /* Añade espacio entre los elementos */
      text-align: center; /* Centra el texto de las columnas */
    }
  
    .Footer-text h3 {
      font-size: 16px; /* Reduce el tamaño del encabezado */
      text-align: center; /* Centra los encabezados */
    }
  
    .Footer-text ul p {
      margin: 10px 0; /* Ajusta márgenes para un diseño compacto */
    }
  
    .Footer-text-B p {
      margin: 10px 20px; /* Reduce márgenes laterales */
      font-size: 14px; /* Reduce tamaño de fuente */
    }
  
    .Wrap-Footer .Footer-Logo {
      height: 50px; /* Reduce el tamaño del logo */
      margin: 0 auto; /* Centra el logo */
    }
    .Footer-Logo{
        align-items: center;
    }
    .Container-Footer-B {
      padding: 10px 0; /* Reduce padding para un diseño más compacto */
    }
  
    .F-izq {
      font-size: medium; /* Ajusta el tamaño del texto */
      text-align: center; /* Centra el texto */
    }
  }
  