.Container-TSC{
    display: flex;
    flex-direction: column ;
    flex-wrap: wrap;
    position: relative;
    grid-row-gap:10px;
    justify-content: center; /* Centra las imágenes horizontalmente */
    align-items: center; /* Centra las imágenes verticalmente */
    justify-items: center;
    margin: 20px;
}
.Container-TSC h1{
    color: #1F1F1F;

}
.Container-FS{
    justify-items: start;
}
.Container-SS{
    justify-items: end;
}
.Container-FS, .Container-SS{
    display:grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 10px;
    justify-content: space-between;
    align-items: stretch;

}

.FS-img, .SS-img{
    width: 33vw;

}
.FS-img{
    align-self: end;

}
.SS-img{
    align-items: start;
}
.FS-text, .SS-text{
    display: grid;
    align-items: center;
    align-content: center;
    width: 33vw;
}

.FS-text{

    text-align: left;
}

.SS-text{
    text-align: left;
}
.FS-text p, .FS-text h2{
    color: #1F1F1F;
}
.SS-text p, .SS-text h2{
    color: #1F1F1F;
}
@media (max-width: 768px) {
    .Container-TSC {
      grid-row-gap: 20px; /* Aumenta el espacio entre elementos */
      margin: 10px; /* Reduce el margen */
    }
  
    .Container-TSC h1 {
      font-size: 22px; /* Reduce el tamaño de la fuente */
      text-align: center; /* Centra el texto */
    }
  
    .Container-FS, .Container-SS {
      grid-template-columns: 1fr; /* Una sola columna */
      grid-gap: 15px; /* Espacio entre filas */
      justify-content: center; /* Centra el contenido */
      max-width: 90vw;
    }
  
    .FS-img, .SS-img {
      width: 80vw; /* Imagen más grande y responsiva */
      height: auto; /* Mantiene la proporción */
    }
  
    .FS-text, .SS-text {
      width: 90%; /* Ocupa casi todo el ancho disponible */
      text-align: left; /* Centra el texto */
      margin: 0 auto; /* Centra dentro del contenedor */
    }
  
    .FS-text p, .SS-text p, 
    .FS-text h2, .SS-text h2 {
      font-size: 16px; /* Reduce el tamaño de los textos */
    }
  }
  
  @media (max-width: 480px) {
    .Container-TSC {
      grid-row-gap: 15px; /* Ajusta el espacio entre elementos */
      margin: 5px; /* Menor margen para pantallas muy pequeñas */
    }
  
    .Container-TSC h1 {
      font-size: 18px; /* Fuente más pequeña */
    }
  
    .Container-FS, .Container-SS {
      grid-template-columns: 1fr; /* Siempre una sola columna */
      grid-gap: 10px; /* Menor espaciado entre elementos */
    }
  
    .FS-img, .SS-img {
      width: 95vw; /* Ocupa casi todo el ancho de la pantalla */
      height: auto; /* Mantiene la proporción */
    }
  
    .FS-text, .SS-text {
      width: 100%; /* Ocupa todo el ancho */
      text-align: left; /* Centra el texto */
      font-size: 14px; /* Fuente más pequeña */
    }
  
    .FS-text p, .SS-text p, 
    .FS-text h2, .SS-text h2 {
      font-size: 14px; /* Ajusta aún más el tamaño del texto */
    }
  }
  