.Container-TopSelectorbySides {
  display: grid;
  overflow: hidden;
}

.Container-TopSelectorbySides h2 {
  font-size: 26px;
  font-weight: bold;
  color: #1F1F1F;
  text-align: center;
}

/* Estilo para el top side */
.Container-TSSTopSide {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr)); /* En pantallas grandes */
  width: 100%;
  height: 12vh;
  justify-content: center;
}

/* Estilo de los botones */
.TopSideButton {
  width: 100%;
  height: 100%;
  border: 0;
  background-color: #F5F7F8;
  color: #1F1F1F;
  border-bottom-width: 10px;
  border-bottom-style: solid;
  border-bottom-color: #2E2E2E;
  position: relative;
  z-index: 1;
}

.TopSideButton h3 {
  font-size: 20px;
  transition: font-size 0.5s ease-in-out;
  color: #2E2E2E;
}

.TopSideButton:hover {
  border-bottom-color: #F4CE14;
  cursor: pointer;
}

.TopSideButton:hover h3 {
  font-size: 24px;
}

.Container-TSSBotSide {
  background-color: #1F1F1F;
  z-index: 0;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: auto auto;
  justify-content: start;
  align-items: center;
}

.Img-TSbS {
  height: auto;
  max-width: 864px;
}

.TSSBotSide-text {
  width: 80%;
  height: 100%;
  color: #F5F7F8;
  margin-left: 100px;
  margin-top: 30px;
}

.TopSideButton.active {
  border-bottom-color: #F4CE14;
}

/* Media Query para pantallas más pequeñas */
@media (max-width: 768px) {
  .Container-TopSelectorbySides {
      grid-template-columns: 1fr; /* Convertir en una sola columna */
      padding: 10px; /* Agregar espacio alrededor */
  }

  .Container-TopSelectorbySides h2 {
      font-size: 22px; /* Reducir tamaño de la fuente */
      text-align: center; /* Centrar para móviles */
  }

  .Container-TSSTopSide {
      display: block; /* Mostrar los elementos como bloque en móviles */
      height: auto;
      overflow-y: auto; /* Permite el desplazamiento vertical */
  }

  .TopSideButton {
      height: auto;
      padding: 10px;
      border-bottom-width: 5px;
  }

  .TopSideButton h3 {
      font-size: 18px;
  }

  .TopSideButton:hover h3 {
      font-size: 20px;
  }

  .Container-TSSBotSide {
      grid-template-columns: 1fr;
      justify-content: center;
      align-items: start;
  }

  .Img-TSbS {
      max-width: 100%;
      margin: 0 auto;
  }

  .TSSBotSide-text {
      width: 90%;
      margin: 20px auto;
      font-size: 14px;
  }
}

/* Media Query para pantallas muy pequeñas */
@media (max-width: 480px) {
  .Container-TopSelectorbySides h2 {
      font-size: 20px;
  }

  .Container-TSSTopSide {
      grid-template-columns: 1fr;
      gap: 10px; /* Agregar espacio entre botones */
  }

  .TopSideButton h3 {
      font-size: 16px;
  }

  .TopSideButton:hover h3 {
      font-size: 18px;
  }

  .Img-TSbS {
      max-width: 100%;
      height: auto;
  }

  .TSSBotSide-text {
      width: 95%;
      margin: 10px auto;
      font-size: 12px;
  }
}
