/* Estilo general del header */
.Header {
  position: fixed;
  top: 0;
  width: 99.8%;
  background-color: #f5f7f8;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
  z-index: 1000;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.Container-Header {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-evenly;
}

.logo {
  height: 50px;
}

/* Menú principal */
.menu {
  display: flex;
  align-items: center;
  gap: 15px;
}

.menu button {
  background: none;
  border: none;
  cursor: pointer;
  transition: border 0.2s ease;
  transition: scale 0.3s ease;

}

.Linkto {
  text-decoration: none;
  color: #1f1f1f;
  font-size: 16px;
}

/* Botón de menú para mobile */
.menu-toggle {
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  display: none; /* Oculto en pantallas grandes */
  color:#1f1f1f;
  transition: color 0.3s ease-in-out;
}
.menu-toggle:hover{
  color:#F4CE14;
}

/* Vista de menú en pantallas pequeñas */
.menu.open {
  display: flex;
  flex-direction: row;
  gap: 10px;
}

.menu button:hover{
  border-bottom: 3px solid #F4CE14;
  scale:1.15;
}

@media screen and (max-width: 720px) {
  .menu {
    display: none; /* Ocultar el menú horizontal por defecto */
    position: absolute;
    top: 70px;
    right: 20px;
    background: #f5f7f8;
    padding: 10px 20px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    flex-direction: column;
    align-items: flex-start;
  }

  .menu.open {
    display: flex; /* Mostrar el menú desplegable cuando esté abierto */
    flex-direction: column;   
  }

  .menu-toggle {
    display: block; /* Mostrar el botón de menú en pantallas pequeñas */
  }
}
