.container-ExpandibleCards {
    width: calc(100% - 100px);
    height: 450px;
    display: flex;
    gap: 10px;
    margin-inline: 50px;
    position: relative;
}

.Card {
    height: 100%;
    border-radius: 30px;
    display: flex;
    align-items: flex-end;
    position: relative;
    min-width: 0px; /* Asegura que se pueda reducir */
    transition: width 0.5s ease;
    width: calc(100% / 6); /* Todas las tarjetas tienen el mismo tamaño inicialmente */
}

.Card.expanded {
    width: 200%; /* La tarjeta activa se expande */
    z-index: 1; /* Asegura que la tarjeta expandida esté encima */
}

.Card img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 30px;
    transition: transform 0.5s ease;
}

.Card-Content {
    position: absolute;
    bottom: 20px;
    left: 10px;
    z-index: 10;
    color: white;
    opacity: 0;
    transform: translateY(100%);
    transition: opacity 0.5s ease, transform 0.5s ease;
}

.Card.expanded .Card-Content {
    opacity: 1;
    transform: translateY(0); /* Muestra el contenido al expandir */
}

.Backdrop {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5); /* Oscurece la imagen ligeramente */
    transition: opacity 0.5s ease;
    border-radius: 30px;
}

.Card.expanded .Backdrop {
    opacity: 0.2; /* Hace más tenue el fondo oscuro en la tarjeta expandida */
}
@media (max-width: 1024px) {
    .container-ExpandibleCards {
      height: auto; /* Permite que la altura se ajuste automáticamente */
      flex-wrap: wrap; /* Las tarjetas se envuelven en múltiples filas si es necesario */
      gap: 20px; /* Espaciado más amplio para pantallas medianas */
      margin-inline: 20px; /* Reduce el margen lateral */
    }
  
    .Card {
      width: calc(100% / 3); /* Muestra 3 tarjetas por fila */
    }
  
    .Card.expanded {
      width: 100%; /* La tarjeta expandida ocupa toda la fila */
    }
  }
  
  @media (max-width: 768px) {
    .container-ExpandibleCards {
      flex-direction: column; /* Cambia el diseño a una columna */
      align-items: center;
      height: auto; /* Altura automática */
      gap: 15px; /* Reduce el espaciado */
      margin-inline: 10px; /* Ajusta los márgenes laterales */
    }
  
    .Card {
      width: 100%; /* Las tarjetas ocupan el ancho completo */
      min-height: 200px; /* Ajusta la altura mínima */
    }
  
    .Card.expanded {
      width: 100%; /* La tarjeta expandida ocupa todo el ancho disponible */
    }
  
    .Card img {
      border-radius: 20px; /* Reduce el radio de las esquinas */
    }
  }
  
  @media (max-width: 480px) {
    .container-ExpandibleCards {
      gap: 10px; /* Espaciado más pequeño */
    }
  
    .Card {
      min-height: 150px; /* Reduce la altura mínima para móviles */
    }
  
    .Card img {
      border-radius: 15px; /* Esquinas más suaves para pantallas pequeñas */
    }
  
    .Card-Content {
      font-size: 14px; /* Reduce el tamaño del contenido */
      bottom: 10px; /* Ajusta la posición del contenido */
      left: 5px; /* Ajusta el margen izquierdo */
    }
  
    .Backdrop {
      border-radius: 15px; /* Ajusta el radio del fondo oscuro */
    }
  }
  