.container-ContainerExpCompania{
    display: grid;
    margin: 0px auto;
    overflow:hidden;
}

.container-CExpC-Top{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(20px, 1fr));     
    justify-content: center;
    justify-items: stretch;
    max-height: 150px;
    margin-bottom: 5px;
}

.CExpC-Buttonimg{
    width: auto;
    height: 100%;
    border-bottom: 5px solid #1F1F1F;
    transition: transform 0.5s ease-in-out;
    transition: border-color 0.5s ease;
    transition: filter 0.5 ease-in-out;
    filter: grayscale(100%); /* Convierte la imagen a blanco y negro */
    
}
.CExpC-Buttonimg:hover{
    border-bottom: 5px solid #F4CE14;
    transform: scale(1.1);
    filter: grayscale(0%); /* Convierte la imagen a color */
    z-index: 2;
}
.CExpC-Buttonimg.active {
    border-bottom: 5px solid #F4CE14; /* Cambia el color del borde cuando está activa */
    transform: scale(1.1); /* Aplica un efecto especial a la imagen activa */
    filter: grayscale(0%); /* Convierte la imagen a color*/
    z-index: 2;
}

.container-CExpC-Bot{
    display: grid;
    grid-template-columns: 1fr 1fr;
    max-height: 480px;
    
}
.container-CExpC-BotA{
    max-height: 480px;
}

.CExpC-FocusImg{
    max-width: 854px;
    width: auto;
    height: 100%
}

.container-CExpC-BotB{
    margin: 0px 50px;
    width: auto;
    overflow-x: auto;
}/* Media query para dispositivos móviles */
@media (max-width: 768px) {
    .container-ContainerExpCompania {
        display: flex;
        flex-direction: column; /* Cambia a layout de columna */
        margin: 0 auto;
        overflow: hidden;
    }

    .container-CExpC-Top {
        grid-template-columns: repeat(auto-fit, minmax(50px, 1fr)); /* Ajusta las imágenes a un tamaño menor */
        max-height: 100px; /* Reduce la altura de las imágenes en la parte superior */
        margin-bottom: 10px;
    }

    .CExpC-Buttonimg {
        height: auto;
        border-bottom-width: 3px; /* Reduce el ancho del borde inferior */
        transition: transform 0.3s ease-in-out, filter 0.3s ease-in-out; /* Ajusta la transición para que sea más rápida */
    }

    .container-CExpC-Bot {
        grid-template-columns: 1fr; /* Cambia a una sola columna para el contenido inferior */
        max-height: auto; /* Permite altura flexible */
    }

    .container-CExpC-BotA {
        max-width: 100%;
        max-height: auto; /* Permite que la imagen se ajuste */
    }

    .CExpC-FocusImg {
        max-width: 100%; /* Asegura que la imagen ocupe todo el ancho disponible */
        height: auto; /* Ajusta la altura automáticamente */
    }

    .container-CExpC-BotB {
        margin: 0px 20px; /* Reduce los márgenes laterales */
        overflow-x: auto; /* Permite desplazamiento horizontal si es necesario */
    }
}
