.MisionContent{
    padding-top: 15px;
    padding-bottom: 15px;
    background-color: #2E2E2E;

}

.MisionC-MainContainer{
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: center;
    margin-left: 16%;
    margin-right: 16%;
}

.MisionC-ContainerB p, .MisionC-ContainerB h2, .MisionC-ContainerA p, .MisionC-ContainerA h2 {
    color: #F5F7F8;
}

.MisionC-TitleWrapper h2{
    margin-bottom: 40px;
}

@media (max-width: 768px) {
    .MisionC-MainContainer {
      grid-template-columns: 1fr; /* Cambia a una sola columna en pantallas más pequeñas */
      margin-left: 5%; /* Reduce los márgenes */
      margin-right: 5%;
    }
  
    .MisionC-TitleWrapper h2 {
      font-size: 24px; /* Reduce el tamaño del texto */
      text-align: center; /* Centra el título */
      margin-bottom: 20px; /* Reduce el margen inferior */
    }
  
    .MisionC-ContainerB p, .MisionC-ContainerB h2, .MisionC-ContainerA p, .MisionC-ContainerA h2 {
      font-size: 16px; /* Reduce el tamaño del texto para pantallas más pequeñas */
      line-height: 1.5; /* Mejora la legibilidad */
    }
  
    .MisionContent {
      padding-top: 10px; /* Reduce el padding superior */
      padding-bottom: 10px; /* Reduce el padding inferior */
    }
  }
  
  @media (max-width: 480px) {
    .MisionC-MainContainer {
      grid-template-columns: 1fr; /* Sigue una sola columna para pantallas muy pequeñas */
      margin-left: 2%; /* Ajusta los márgenes para pantallas pequeñas */
      margin-right: 2%;
    }
  
    .MisionC-TitleWrapper h2 {
      font-size: 20px; /* Reduce aún más el tamaño del texto */
    }
  
    .MisionC-ContainerB p, .MisionC-ContainerB h2, .MisionC-ContainerA p, .MisionC-ContainerA h2 {
      font-size: 14px; /* Ajusta el tamaño del texto */
    }
  
    .MisionContent {
      padding-top: 8px; /* Reduce el padding superior */
      padding-bottom: 8px; /* Reduce el padding inferior */
    }
  }
  