.Container-GaleryViewer {
    display: flex;
    position: relative;
    width: 100vw -5px; /* Ancho de la pantalla completa */
    height: 640px; /* Altura fija de 720px */
    justify-content: center; /* Centra las imágenes horizontalmente */
    align-items: center; /* Centra las imágenes verticalmente */
    overflow: hidden; /* Evita que el contenido se desborde */
}

.img-Galery {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: nowrap; /* Evita que los elementos se envuelvan y causen el desbordamiento */
    width: 100%; /* Asegura que el contenedor ocupe el ancho completo */
    height: 100%; /* Asegura que el contenedor ocupe la altura completa */
}

.img-Galery img {
    width: 100%; /* Ocupa el ancho completo de la pantalla */
    height: 100%; /* Altura fija */
    object-fit: cover; /* Se ajusta para rellenar el contenedor sin distorsionar */
    opacity: 1; /* Estado inicial de opacidad plena */
    transition: opacity 0.3s ease-in-out;
    
}

.icon-left, .icon-right {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 50%; /* Coloca los iconos en el centro verticalmente */
    transform: translateY(-50%); /* Centra los iconos verticalmente */
    z-index: 1; /* Asegura que el ícono esté encima de la imagen */
    color: white;
    width: 50px; /* Ajusta el ancho del ícono */
    height: 50px; /* Ajusta el alto del ícono */
    transition: opacity 0.3s ease;
    opacity: 35%;
    cursor: pointer;
}

.icon-left {
    left: 10px; /* A 10px del borde izquierdo */
}

.icon-right {
    right: 10px; /* A 10px del borde derecho */
}

.icon-left:hover, .icon-right:hover {
    opacity: 100%;
}

.page-indicator {
    position: absolute;
    bottom: 25px;
    left: 50%;
    transform: translateX(-50%);
    color: #333;
    font-size: 16px;
    background: rgba(255, 255, 255, 0.7);
    padding: 5px 10px;
    border-radius: 5px;
    transition: opacity 0.5s ease;
    opacity: 25%;
}

.Container-GaleryViewer:hover .page-indicator {
    opacity: 90%; /* Aumenta la opacidad cuando se hace hover */
}

.img-Galery.fade-out img {
    opacity: 0;
}

.img-Galery.fade-in img {
    opacity: 1;
}
@media (max-width: 768px) {
    .Container-GaleryViewer {
      height: 360px; /* Reduce la altura para dispositivos más pequeños */
    }
  
    .img-Galery img {
      object-fit: contain; /* Ajusta para que las imágenes no se corten en pantallas pequeñas */
    }
  
    .icon-left, .icon-right {
      width: 40px; /* Reduce el tamaño de los íconos */
      height: 40px;
      top: 45%; /* Ajusta la posición vertical */
      font-size: 1.5em; /* Ajusta el tamaño del ícono */
    }
  
    .page-indicator {
      font-size: 12px; /* Reduce el tamaño del texto */
      bottom: 15px; /* Ajusta la posición inferior */
      padding: 3px 8px; /* Reduce el relleno */
    }
  }
  
  @media (max-width: 480px) {
    .Container-GaleryViewer {
      height: 280px; /* Altura más pequeña para pantallas muy pequeñas */
    }
  
    .icon-left, .icon-right {
      width: 30px; /* Íconos aún más pequeños */
      height: 30px;
      top: 50%; /* Centrado vertical */
      font-size: 1.2em; /* Reduce el tamaño del ícono */
    }
  
    .page-indicator {
      font-size: 10px; /* Reduce aún más el tamaño del texto */
      bottom: 10px; /* Ajusta la posición inferior */
    }
  }
  