.companies-carousel {
    display: flex;
    margin-left: 200px;
    margin-right: 200px;
    width: calc(100% - 400px);
    height: 150px; /* Altura fija para el carrusel */
    overflow: hidden; /* Oculta el contenido que desborda */
    position: relative;
}
.Container-Companies h2{
    text-align: center;
    color: #1F1F1F;
}

.companies-item {
    display: flex;
    flex-direction: row;
    animation: scroll 20s linear infinite; /* La animación es lineal y continua */
    width: calc(200% + 2500px); /* Duplicamos el ancho del contenedor para lograr el efecto de bucle */
}

.carousel-item {
    flex: 0 0 auto; /* Evita que los elementos se reduzcan */
    width: 110px;
    height: 110px;
    margin: 10px;
    transition: transform 0.3s ease-in-out, width 0.3s ease-in-out, height 0.3s ease-in-out, margin 0.3s ease-in-out;
}
.carousel-item:hover{
    width: 150px;
    height: 150px;
    margin: 0;
}

.carousel-item img {
    width: 100%;
    height: 100%;
    object-fit: cover; /* Asegura que las imágenes cubran todo el contenedor sin deformarse */
    display: block;
}

@keyframes scroll {
    0% {
        transform: translateX(0);
    }
    100% {
        transform: translateX(-50%); /* Desplaza el contenido por la mitad, que es el área duplicada */
    }}

    @media (max-width: 768px) {
        .companies-carousel {
          margin-left: 50px; /* Reduce los márgenes para pantallas medianas */
          margin-right: 50px;
          width: calc(100% - 100px); /* Ajusta el ancho total */
          height: 120px; /* Reduce la altura */
        }
      
        .companies-item {
          animation: scroll 15s linear infinite; /* Ajusta la velocidad de la animación para pantallas medianas */
          width: calc(200% + 1000px); /* Reduce el ancho del contenido */
        }
      
        .carousel-item {
          width: 90px; /* Reduce el tamaño de los ítems */
          height: 90px;
          margin: 8px;
        }
      
        .carousel-item:hover {
          width: 120px; /* Ajusta el tamaño al hacer hover */
          height: 120px;
        }
      }
      
      @media (max-width: 480px) {
        .companies-carousel {
          margin-left: 20px; /* Márgenes más pequeños */
          margin-right: 20px;
          width: calc(100% - 40px); /* Ancho total ajustado */
          height: 100px; /* Menor altura para móviles */
        }
      
        .companies-item {
          animation: scroll 10s linear infinite; /* Más rápido para pantallas pequeñas */
          width: calc(200% + 500px); /* Ajusta el contenido al área más pequeña */
        }
      
        .carousel-item {
          width: 70px; /* Tamaño reducido de los ítems */
          height: 70px;
          margin: 5px;
        }
      
        .carousel-item:hover {
          width: 100px; /* Menor incremento en el hover */
          height: 100px;
        }
      }
      