.Recertificados-Container form{
    display: flex;
    flex-direction: column;
     align-items: center;
}

.Recertificados-Container{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.select-accesorio{
    display: flex;
    flex-direction: row;
    
}
.accesorio-container{
    padding: 20px;
    display: flex;
    flex-direction: column;
}
.Recertificados-Container select{
    padding: 5px;
    margin: 5px;
}

.options-main-container{
    align-items: center;
    display: flex;
    flex-direction: column;
}

@media (max-width: 768px) {
    .select-accesorio{
        display: flex;
        flex-direction: column;
    }
}