  .Container-Formulario {
    padding: 20px;
    display: grid;
    grid-template-rows: auto auto auto auto;
    gap: 20px;
    width: 100%; /* Asegura que tome el 100% del ancho disponible */
    max-width: 50vw; /* Controla el ancho máximo del formulario */
    background-color: #FFFFFF;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin: 0 auto; /* Márgenes automáticos para centrar el formulario */
    box-sizing: border-box;

    margin-bottom: 25px;
  }

  .Container-Formulario ul{
    padding-left: 10px;
    padding-right: 10px;
  }
  .Form-Row {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }
  
  input, textarea {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
    box-sizing: border-box;
  }
  
  input:focus, textarea:focus {
    outline: none;
    border-color: #F4CE14;
    box-shadow: 0 0 5px rgba(244, 206, 20, 0.5);
  }
  
  .Container-Formulario p {
    margin: 0;
    font-size: 16px;
    color: #1F1F1F;
  }
  
  .submit-Contacto {
    background-color: #F4CE14;
    color: #1F1F1F;
    border: none;
    border-radius: 4px;
    padding: 10px 15px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    display: block;
    margin: 0 auto;
  }
  
  .submit-Contacto:hover {
    background-color: #E5B013;
  }
  
  .submit-Contacto:focus {
    outline: none;
  }

  .Container-OtrosMC{
    position: relative;
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-bottom: 25px;
  }
  .Contacto-Text{
   width: 100%;
   height: 100%;
   background-color: #1F1F1F;

  }

  .Container-Contacto-Text{
    margin-right: 200px;
    display: grid;
    grid-template-columns: 1fr 1fr;

  }

  .Contacto-Text ul p, .Contacto-Text h3{
    color: #F5F7F8;
  }
  .Contacto-Text h3{
    margin-left: 200px;
  }

  .izq{
    text-align: right;
  }

  @media (max-width: 768px) {
    .Container-Formulario {
      padding: 15px;
      width: 90%; /* Reduce el ancho del formulario */
      max-width: none; /* Elimina el límite de ancho para ajustarse a pantallas más pequeñas */
      gap: 15px; /* Reduce el espacio entre elementos */
    }
  
    .Form-Row {
      grid-template-columns: 1fr; /* Coloca los campos en una sola columna */
      gap: 15px; /* Reduce el espacio entre los campos */
    }
  
    input, textarea {
      font-size: 14px; /* Reduce el tamaño de la fuente */
      padding: 8px; /* Reduce el padding para ahorrar espacio */
    }
  
    .submit-Contacto {
      font-size: 14px; /* Reduce el tamaño del botón */
      padding: 8px 10px; /* Ajusta el padding del botón */
    }
  
    .Container-OtrosMC {
      grid-template-columns: 1fr; /* Cambia a una columna */
      gap: 20px; /* Espacio entre los elementos */
    }
  
    .Contacto-Text h3 {
      margin-left: 0; /* Centra el encabezado en móviles */
      text-align: center;
    }
  
    .Contacto-Text ul p, .Contacto-Text h3 {
      font-size: 14px; /* Reduce el tamaño de los textos */
      text-align: center; /* Centra el texto en móviles */
    }
  
    .Container-Contacto-Text {
      grid-template-columns: 1fr; /* Cambia a una columna */
      margin-right: 0; /* Elimina el margen lateral */
      gap: 15px; /* Espacio entre los elementos */
    }
  
    .izq {
      text-align: center; /* Centra el texto en dispositivos móviles */
    }
  }
  